body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
  }
  
  .success {
    background: rgb(28, 184, 65);
    font-size: 18px;
    border-style: hidden;
    border-radius: 5px;
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  
  .error {
    background: rgb(184, 28, 28);
    font-size: 18px;
    border-style: hidden;
    border-radius: 5px;
    padding: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
  }


